<template>
  <div class="article-management">
    <admin-default :selectMenuIndex="'2'">
      <div slot="main">
        <!--  -->
        <div class="opera-wrap">
          <el-button type="primary" @click="addArticleShow"></el-button>
          <el-button :disabled="!ids.length" type="danger" @click="deleteChecked(ids)"></el-button>
          <el-input placeholder="" v-model="searchInput" class="input-with-select">
            <el-select class="select" v-model="select" slot="prepend" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
            <el-button
              @click="getArticleData({type:select,keyword:searchInput})"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </div>
        <!--  -->
        <div class="tab-wrap">
          <el-table
            border
            highlight-current-row
            :data="articleListData"
            style="width: 100%"
            max-height="470"
            @selection-change="handleSelectionChange"
          >
            <el-table-column align="center" type="selection"></el-table-column>
            <el-table-column align="center" label="">
              <template slot-scope="scope">
                <p>{{ formatTime(new Date(scope.row.createtime))}}</p>
              </template>
            </el-table-column>
            <el-table-column align="center" prop="title" label=""></el-table-column>
            <el-table-column show-overflow-tooltip align="center" prop="desc" label=""></el-table-column>
            <el-table-column align="center" label="">
              <template slot-scope="scope">
                <img
                  width="100%"
                  :src="scope.row.bannerimg?scope.row.bannerimg:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1558659946263&di=83a1047b593eb9dd0dc19f1666aae116&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201703%2F30%2F20170330225122_TLMun.jpeg'"
                  alt=""
                >
                <span>[]</span>
              </template>
            </el-table-column>
            <el-table-column align="center" prop="top" label="">
              <template slot-scope="scope">
                <span>{{ scope.row.top == 0? '':'' }}</span>
              </template>
            </el-table-column>
            <el-table-column align="center" prop="articlestate" label="">
              <template slot-scope="scope">
                <span>{{ scope.row.articlestate == 0? '':'' }}</span>
              </template>
            </el-table-column>
            <el-table-column align="center" prop="name" label=""></el-table-column>
            <el-table-column align="center" fixed="right" prop="opera" label="">
              <template slot-scope="scope">
                <el-button
                  @click.native.prevent="editRow(scope.$index, articleListData)"
                  type="primary"
                  size="mini"
                ></el-button>
                <el-button
                  @click.native.prevent="deleteRow(scope.$index, articleListData)"
                  type="danger"
                  size="mini"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <!--  -->
        <div class="pagation-wrap">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagationParams.currentPage"
            :page-sizes="[1, 10, 20, 50, 100, 200, 500]"
            :page-size="pagationParams.limit"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagationParams.totalcount"
          ></el-pagination>
        </div>

        <!--  -->
        <add-article :articleTypeData="articleTypeData" ref="add-article"></add-article>

        <!--  -->
        <edit-article
          :articleTypeData="articleTypeData"
          :editArticleData="editArticleData"
          ref="edit-article"
        ></edit-article>
      </div>
    </admin-default>
  </div>
</template>

<script>
import { MessageBox, Notification } from "element-ui";
import {
  getArticleTypeData,
  getArticleData,
  deleteArticleData
} from "api/article.js";
import { formatTime } from "common/js/util";
import AdminDefault from "./default.vue";
import EditArticle from "components/admin/article/edit-article.vue";
import AddArticle from "components/admin/article/add-article.vue";
export default {
  data() {
    return {
      articleListData: [],
      pagationParams: {
        currentPage: 1,
        totalcount: 100,
        limit: 50
      },
      select: "",
      searchInput: "",
      articleTypeData: [],
      ids: [], //  id
      editArticleData: {
        articlestate: 1
      }
    };
  },
  methods: {
    //   model
    addArticleShow() {
      this.$refs["add-article"].dialogVisible = true;
    },
    //   
    getArticleTypeData() {
      getArticleTypeData().then(res => {
        // , 
        let tempArr = [];
        for (let i = 0; i < res.length; i++) {
          if (res[i].pid === 0) {
            tempArr.push({
              label: res[i].name,
              id: res[i].id,
              options: []
            });
          }
          for (let j = 0; j < res.length; j++) {
            if (res[j].pid === res[i].id) {
              tempArr[i]["options"].push({
                label: res[j].name,
                id: res[j].id
              });
            }
          }
        }
        this.articleTypeData = tempArr;
      });
    },
    //   
    getArticleData(params) {
      getArticleData(params).then(res => {
        this.articleListData = res.data;
        this.pagationParams.totalcount = res.totalcount;
      });
    },
    //   
    handleSelectionChange(selection) {
      let ids = new Array();
      selection.forEach(item => {
        ids.push(item.id);
      });
      this.ids = ids;
    },
    //  
    handleSizeChange(val) {
      this.pagationParams.limit = val;
      this.getArticleData({
        type: this.select,
        keyword: this.searchInput,
        limit: this.pagationParams.limit,
        currentPage: this.pagationParams.currentPage
      });
    },
    //  
    handleCurrentChange(val) {
      this.pagationParams.currentPage = val;
      this.getArticleData({
        type: this.select,
        keyword: this.searchInput,
        limit: this.pagationParams.limit,
        currentPage: this.pagationParams.currentPage
      });
    },
    //  
    editRow(index, rows) {
      this.editArticleData = Object.assign(
        {},
        this.editArticleData,
        this.articleListData.find(item => item.id == rows[index].id)
      );
      this.$refs["edit-article"].editDialogVisible = true;
    },
    //  
    deleteRow(index, rows) {
      MessageBox.confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          //  
          deleteArticleData({ id: rows[index].id }).then(res => {
            if (res) {
              //  
              rows.splice(index, 1);
              this.pagationParams.totalcount--;
              Notification({
                title: "",
                message: "",
                type: "success"
              });
            } else {
              Notification({
                title: "",
                message: "",
                type: "error"
              });
            }
          });
        })
        .catch(() => {
          Notification({
            title: "",
            message: "",
            type: "info"
          });
        });
    },
    //  
    deleteChecked(ids) {
      MessageBox.confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          //  
          deleteArticleData({ id: ids }).then(res => {
            if (res) {
              //   -> id
              this.articleListData = this.articleListData.filter(
                (item, index) => ids.findIndex(id => id == item.id) == -1
              );
              this.pagationParams.totalcount--;
              Notification({
                title: "",
                message: "",
                type: "success"
              });
            } else {
              Notification({
                title: "",
                message: "",
                type: "error"
              });
            }
          });
        })
        .catch(() => {
          Notification({
            title: "",
            message: "",
            type: "info"
          });
        });
    },
    formatTime(params) {
      return formatTime(params);
    }
  },
  created() {
    this.getArticleData({
      type: this.select,
      keyword: this.searchInput,
      limit: this.pagationParams.limit,
      currentPage: this.pagationParams.currentPage
    });

    //   
    this.getArticleTypeData();
  },
  components: {
    AdminDefault,
    EditArticle,
    AddArticle
  }
};
</script>

<style lang='stylus' scoped>
@import '~common/stylus/variable.styl';

.article-management {
  .main {
    .opera-wrap {
      display: flex;
      align-items: center;
      margin-bottom: 20px;

      &>* {
        margin-right: 20px;

        &:last-child {
          margin-right: 0;
        }
      }

      .select {
        width: 100px;
      }
    }

    .tab-wrap {
      margin-bottom: 20px;
    }
  }
}
</style>
