<template>
  <div class="add-article.vue">
    <div class="dialog-wrap">
      <el-dialog title="" :visible.sync="dialogVisible" width="80%" :before-close="handleClose">
        <div class="top">
          <el-input placeholder="" v-model="article.title"></el-input>
          <el-date-picker
            v-model="article.createtime"
            type="datetime"
            @change="articleCreateTimeChange"
            placeholder=""
            default-time="12:00:00"
          ></el-date-picker>
          <!--  -->
          <el-select @change="articleTypeChange" v-model="article.type" placeholder="">
            <el-option-group
              v-for="(group, index) in articleTypeData"
              :key="index"
              :label="group.label"
            >
              <el-option
                v-for="(it,id) in group.options"
                :key="id"
                :label="it.label"
                :value="it.id"
              ></el-option>
            </el-option-group>
          </el-select>
          <!--  -->
          <el-select @change="articleTopChange" v-model="article.top" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </div>

        <div class="middle">
          <el-input type="textarea" placeholder="" v-model="article.desc"></el-input>
          <editor ref="editor" @getEditorContent="getEditorContent"></editor>
        </div>

        <div class="footer">
          <div class="imgload-wrap">
            <img-load
              @uploadImageSuccess="articleBannerImgChange"
              action="http://localhost:9001/uploadArticleBannerImg"
            ></img-load>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="commitArticle"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { MessageBox, Notification } from "element-ui";
import { postArticleData } from "api/article.js";
import Editor from "components/admin/common/Editor.vue";
import ImgLoad from "components/admin/common/imgLoad.vue";
export default {
  props: ["articleTypeData"],
  data() {
    return {
      dialogVisible: false,
      article: {
        title: "",
        createtime: "",
        type: "",
        desc: "",
        content: "",
        bannerimg: "",
        top: ""
      }
    };
  },
  methods: {
    //  
    commitArticle() {
      // 
      this.$refs.editor.getContent();
      // 
      postArticleData(this.article)
        .then(res => {
          if (res) {
            Notification({
              title: "",
              message: "",
              type: "success"
            });
            setTimeout(() => {
              this.dialogVisible = false;
            }, 1000);
          } else {
            Notification({
              title: "",
              message: "",
              type: "error"
            });
          }
        })
        .catch(err => {
          Notification({
            title: "",
            message: "",
            type: "error"
          });
        });
    },
    //  
    getEditorContent(text) {
      this.article.content = this.$xss(text);
    },
    //  
    articleTypeChange(val) {
      this.article.type = val;
    },
    //  
    articleCreateTimeChange(val) {
      this.article.createtime = val;
    },
    //  
    articleTopChange(val) {
      this.article.top = val;
    },
    //  
    articleBannerImgChange(response, file, filelist) {
      let fileListArray = [];
      filelist.forEach(item => {
        fileListArray.push(item.response.filename);
      });
      this.article.bannerimg = fileListArray.join("|");
    },
    handleClose(done) {
      MessageBox.confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    }
  },
  components: {
    Editor,
    ImgLoad
  }
};
</script>

<style lang='stylus' scoped>
.dialog-wrap {
  &>>>.el-dialog {
    .el-dialog__body {
      &>* {
        margin-bottom: 20px;
      }

      .top {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &>* {
          width: 22%;
        }
      }

      .middle {
        &>* {
          margin-bottom: 20px;
        }
      }

      .footer {
      }
    }
  }
}
</style>
